:root {
  --sp-s: 8px;
  --sp-m: 16px;
  --sp-l: 24px;
  --sp-xl: 32px;
}

.pageWrapper {
  display: flex;
  flex-direction: column;
  gap: var(--sp-l);
  padding: var(--sp-xl);
  background: white;
  min-height: 100vh;
}

.pageTitle {
  margin-bottom: var(--sp-m);
}

.card {
  max-width: 1000px;
  margin: 0 auto;
  border: 1px solid var(--colorNeutralStroke2);
  border-radius: 4px;
  padding: var(--sp-l);
}

.boxHeader {
  margin-bottom: var(--sp-m);
}

.questionList {
  display: flex;
  flex-direction: column;
}

.questionRow {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: var(--sp-m) 0;
  border-top: 1px solid var(--colorNeutralStroke2);
}

.questionRow:first-child {
  border-top: none;
}

.questionLeft {
  display: flex;
  align-items: center;
  gap: var(--sp-m);
  flex: 1;
  min-width: 0;
}

.questionText {
  font-size: 0.95rem;
  color: var(--colorNeutralForeground1);
  line-height: 1.4;
}

.radioGroup {
  display: flex;
  gap: var(--sp-m);
  flex-shrink: 0;
  align-items: center;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: var(--sp-l);
}

.footerRight {
  display: flex;
  gap: var(--sp-m);
}

@media (max-width: 800px) {
  .questionRow {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--sp-s);
  }

  .radioGroup {
    margin-top: var(--sp-s);
  }

  .footer {
    flex-direction: column;
    align-items: stretch;
    gap: var(--sp-s);
  }
}
